[v-cloak] {
  display: none;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(26, 19, 17, 0.5);
  transition: opacity 500ms;
  visibility: hidden;
  opacity: 0;
  z-index: 9999;
  overflow: scroll;
  opacity: 0;
  transition: transform 0.4s linear;
  animation-duration: 1s;
  animation-fill-mode: both;
  padding-top: 70px;
}

@media (max-width: 768px) {
  .overlay {
    padding-top: 0;
  }
}

.overlay.open {
  visibility: visible;
  opacity: 1;
  transition: .6s;
}

.popup {
  background: #BED5EF;
  width: 100%;
  max-width: 1200px;
  height: 622px;
  margin: auto;
  position: relative;
  padding-top: 64px;
}

@media (max-width: 768px) {
  .popup {
    max-width: 100%;
    margin-top: 77px;
    width: calc(100% - 40px);
    margin-right: auto;
    margin-left: auto;
    padding-top: 0;
    background: none;
  }
  .popup:before {
    position: absolute;
    content: "";
    width: 100%;
    height: 80px;
    background: #fff;
    top: 0;
    left: 0;
  }
}

.popup:before {
  position: absolute;
  content: "";
  width: 32px;
  height: 100%;
  background: url(../images/pop-line.svg) no-repeat;
  left: 0;
  top: 0;
  background-size: 100%;
  background-position: top;
}

@media (max-width: 768px) {
  .popup:before {
    width: 15px;
    background-size: 192%;
  }
}

.popup:after {
  position: absolute;
  content: "";
  width: 32px;
  height: 100%;
  background: url(../images/pop-line.svg) no-repeat;
  right: 0;
  top: 0;
  background-size: 100%;
  background-position: top;
}

@media (max-width: 768px) {
  .popup:after {
    width: 15px;
    background-size: 192%;
  }
}

.popup-content {
  max-width: 843px;
  margin: auto;
}

@media (max-width: 768px) {
